  text-align: center;
}*/

.center {
  display:flex;
  justify-content: center;
}

.spread {
  display: flex;
  justify-content: flex-end;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
}

.header {
  margin-top: -200px;
  width: 100vw;
  height: 1000px;
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;
}

.cont-grid{
  margin: 0px auto;
  margin-top: 10vh;  
  width: 80vw;
  min-height: 600px;
  height: auto;
  display: grid;
  background: linear-gradient(180deg, rgba(6,74,18,1) 0%, rgba(63,134,89,1) 49%, rgba(119,217,203,1) 100%);  
  box-shadow: 10px 10px 10px 10px black; 
  grid-template-columns: 5% 20% 20% 50% 5%;
  grid-template-rows: 25% 25% 40% auto;
}

.item-a {
  display: grid;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}

.item-b {
  font-size: 4.5rem;
  color: white;
  font-family: sans-serif;
  font-weight: 800;
  grid-column-start: 3;  
  grid-row-start: 1;
  grid-row-end: 3;
  /* background-color: white; */
}

.item-c {
  grid-column-start: 2;
  grid-row-start: 2;
}

.item-d {
  grid-column-start: 2;
  grid-column-end: 4;
  grid-row-start: 3;
  margin-top: 100px;
}

.item-e {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 5;
  margin-left: 0px auto;
}

/* media querries */

@media only screen and (max-width: 1100px) {
  .item-d {
    grid-column-end: 5;
  }

  .item-b {
    font-size: 3rem; 
  }

  .number {
    margin-top: 1rem;
  }
}

@media only screen and (max-width: 800px) {
  .cont-grid {
    width: 95vw;
    margin: 1rem auto;
  }
}

@media only screen and (max-width: 600px) {
  .cont-grid{    
    width: 90vw;
    margin: 2rem auto;
    padding: 1rem; 
    height: 120vh;   
    background: linear-gradient(180deg, rgba(6,74,18,1) 0%, rgba(63,134,89,1) 49%, rgba(119,217,203,1) 100%);  
    box-shadow: 10px 10px 10px 10px black;    
    grid-template-columns: 5% 45% 45% 5%;
    grid-template-rows: 10% 10% 50% 10%;
  }

  .item-a {    
    grid-column-start: 2;    
    grid-row-start: 1;
    }
  
  .item-b {    
    grid-column-start: 3;  
    grid-row-start: 1; 
  }
  
  .item-c {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 2;  
  }
  
  .item-d {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 4;  
    margin-top: 7rem;
  }
  
  .item-e {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 3; 
    margin-left: 0px auto;
  }
}